<template>
  <div class="L-player" :class="{'Lv-hide':!show}">
    <video-player
      class="vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
      @pause="onPlayerPause"
      @ended="onPlayerEnded"
      @loadeddata="onPlayerLoadeddata"
      @timeupdate="onPlayerTimeUpdate"
    ></video-player>
    <div class="prevent-click-play"></div>
  </div>
</template>
<script>
// import videojs from "video.js";
import { videoPlayer } from "vue-video-player";
import "video.js/dist/video-js.css";
import "videojs-flash";
import "videojs-contrib-hls";
export default {
  /*options{
        type:"", //mp4、rtmp、flv、m3u8
        src: "",
        volume: num,
        disable: boolean
        post: String //默认图片
    }*/
  /*
        加载完成：@loaded return{length秒数,volume},
        播放状态：@status return num or pause/end
    */
  props: ["options", "play", "current", "volume", "isSelect", "show", "rePlay"],
  components: {
    videoPlayer
  },
  data() {
    return {
      done: false,
      //播放器配置
      playerOptions: {
        autoplay: true,
        muted: false,
        loop: false,
        preload: "auto",
        language: "zh-CN",
        errorDisplay: false,
        controlBar: false,
        aspectRatio: "16:9",
        fluid: true,
        // notSupportedMessage: "视频无法播放，请联系管理员",
        techOrder: ["html5", "flash"],
        flash: { hls: { withCredentials: false } },
        html5: { hls: { withCredentials: false } },
        sources: []
      }
    };
  },
  created() {
    let sourceType = "";
    if (this.options) {
      if (this.options.post) {
        this.playerOptions.poster = this.options.post;
      }
      switch (this.options.type) {
        case "mp4":
          sourceType = "video/mp4";
          break;
        case "rtmp":
          sourceType = "rtmp/mp4";
          break;
        case "flv":
          sourceType = "video/x-flv";
          break;
        case "m3u8":
          sourceType = "application/x-mpegURL";
          break;
        default:
          new Error("videoPlayer: 视频类型不支持");
          break;
      }
      this.playerOptions.sources = [];
      this.playerOptions.sources.push({
        withCredentials: false,
        type: sourceType,
        Duration: this.options.Duration,
        src: this.options.src
      });
      // console.log(this.playerOptions.sources);
    }
  },
  methods: {
    //监听视频暂停
    onPlayerPause($event) {
      try {
        this.$emit("status", "pause");
      } catch (ex) {}
    },
    //监听播放结束
    onPlayerEnded($event) {
      this.done = true;
      if (!this.options.disable) {
        this.$emit("status", "end");
      }
    },
    // 监听加载完成
    onPlayerLoadeddata($event) {
      if (this.isSelect) {
        this.setVolume(this.volume);
      } else {
        this.setVolume(0);
      }

      this.$emit("loaded", {
        length: this.options.Duration / 1000,
        volume: this.player.volume() * 100
      });
    },
    //监听时间变化
    onPlayerTimeUpdate($event) {
      if (!this.options.disable) {
        this.$emit("status", $event.currentTime());
      }
    },
    // 设置音量
    setVolume(parms) {
      if (!isNaN(parms) && parms >= 0 && parms <= 100) {
        this.player.muted(false);
        this.player.volume(parms / 100);
      } else {
        console.warn("videoPlayer: 音量设置失败");
      }
    }
  },
  watch: {
    play(newVal, oldVal) {
      if (newVal) {
        if (!this.done) {
          this.player.play();
        }
      } else {
        this.player.pause();
      }
    },
    current(newVal, oldVal) {
      if (!isNaN(newVal)) {
        if (newVal <= this.player.duration()) {
          this.done = false;
        }
        this.player.currentTime(newVal);
      } else {
        console.warn("videoPlayer: current is NAN");
      }
    },
    volume(newVal, oldVal) {
      if (this.isSelect) {
        this.setVolume(newVal);
      }
    },
    isSelect(newVal, oldVal) {
      if (newVal) {
        this.setVolume(this.volume);
      } else {
        this.setVolume(0);
      }
    },
    rePlay(newVal, oldVal) {
      if (newVal) {
        this.done = false;
      }
    }
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    }
  }
};
</script>
<style lang="less">
//覆盖vue-video-player样式
.L-player {
  width: 100%;
  height: 100%;
  position: absolute;
  .vjs-custom-skin {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    .video-js .vjs-big-play-button {
      display: none !important;
    }
  }
  .prevent-click-play {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 2;
  }
}
.Lv-hide {
  opacity: 0;
}
</style>



