<template>
  <div class="video-box" :class="{'full-screen-mode':isFullScreen, 'terminal-mode': terminal}">
    <div class="play-box" :class="{'mini-player':isMini}">
      <div v-if="!isFlashSupport" class="flash-tip">
        播放该视频需要flash插件支持,请点击
        <a
          href="http://www.adobe.com/go/getflashplayer"
          rel="nofollow"
          target="_blank"
          title="升级Flash插件"
        >启用flash</a>
      </div>
      <!-- 播放器 -->
      <!-- 直播 -->
      <div v-if="isLive &&onlive==1" class="player-container">
        <div v-if="count > 0" class="div_1" :class="{'third':count > 2,'second':count < 3}">
          <div
            v-show="count > 0"
            class="live-video-box"
            :class="{'video1':active==1,'video2':active==2,'video3':active==3}"
            @click="changevideo(1)"
          >
            <video
              ref="h5sVideo"
              autoplay
              muted
              :h5spath="video1path"
              :h5mediawebsocketURL="h5mediawebsocketURL"
            ></video>
            <div class="mask"></div>
          </div>
          <div
            v-show="count > 1"
            class="live-video-box"
            :class="{'video2':active!=2,'video1':active==2}"
            @click="changevideo(2)"
          >
            <video
              ref="h5sVideo2"
              autoplay
              muted
              :h5spath="video2path"
              :h5mediawebsocketURL="h5mediawebsocketURL"
            ></video>
            <div class="mask"></div>
          </div>
          <div
            v-show="count > 2"
            class="live-video-box"
            :class="{'video3':active!=3,'video1':active==3}"
            @click="changevideo(3)"
          >
            <video
              ref="h5sVideo3"
              autoplay
              muted
              :h5spath="video3path"
              :h5mediawebsocketURL="h5mediawebsocketURL"
            ></video>
            <div class="mask"></div>
          </div>
          <!-- 控制栏 -->
          <div class="video-controller">
            <div class="control-left">
              <!-- <i
            class="iconfont start-btn"
            :class="{'icon-bofang':!isPlay,'icon-zanting':isPlay}"
            @click="playHandler"
              ></i>-->
            </div>
            <div class="control-right">
              <!-- 音量 -->
              <div class="volume">
                <i
                  class="iconfont"
                  :class="{'icon-yinliang':!isQuiet,'icon-jingyin':isQuiet}"
                  @click="isQuiet = !isQuiet"
                ></i>
                <el-slider v-model="volume" class="v-progress" vertical height="125px" :max="100"></el-slider>
              </div>
              <!-- 全屏 -->
              <i class="iconfont icon-quanping" @click="fullScreenHandler"></i>
            </div>
          </div>
        </div>
      </div>
      <!--非直播时间段内显示 -->
      <div v-if="isLive &&onlive!=1 && isFlashSupport" class="player-container">
        <img v-if="onlive==0" src="../../../static/zbjs1.png" :draggable="false" />
        <img v-if="onlive==-1" src="../../../static/zb2.png" :draggable="false" />
      </div>
      <!-- 课程资源 -->
      <div v-if="isFlashSupport && !isLive" class="player-container">
        <l-player
          v-if="gotVideo && playerList[0]"
          :class="{'l-player-0':v_watch==0,'l-player-1':v_watch==1,'l-player-2':v_watch==2}"
          :style="{'width':playerList[0].v_split?'':'100%','height':playerList[0].v_split?'':'100%'}"
          :show="playerList[0].show"
          :isSelect="playerList[0].isSelect"
          :options="playerList[0].options"
          :volume="playerList[0].volume"
          :play="isPlay"
          :rePlay="rePlay"
          :current="currentTime"
          @loaded="LLoaded($event,playerList[0])"
          @status="LStatus"
          @click.native="watchIt(0)"
        />
        <l-player
          v-if="gotVideo && playerList[1]"
          :class="{'l-player-1':v_watch!=1,'l-player-0':v_watch==1}"
          :style="{'width':playerList[1].v_split?'':'100%','height':playerList[1].v_split?'':'100%'}"
          :show="playerList[1].show"
          :isSelect="playerList[1].isSelect"
          :options="playerList[1].options"
          :volume="playerList[1].volume"
          :play="isPlay"
          :rePlay="rePlay"
          :current="currentTime"
          @loaded="LLoaded($event,playerList[1])"
          @status="LStatus"
          @click.native="watchIt(1)"
        ></l-player>
        <l-player
          v-if="gotVideo && playerList[2]"
          :class="{'l-player-2':v_watch!=2,'l-player-0':v_watch==2}"
          :style="{'width':playerList[2].v_split?'':'100%','height':playerList[2].v_split?'':'100%'}"
          :show="playerList[2].show"
          :isSelect="playerList[2].isSelect"
          :options="playerList[2].options"
          :volume="playerList[2].volume"
          :play="isPlay"
          :rePlay="rePlay"
          :current="currentTime"
          @loaded="LLoaded($event,playerList[2])"
          @status="LStatus"
          @click.native="watchIt(2)"
        ></l-player>
      </div>
      <!-- 弹幕 -->
      <!-- <l-danmu
        v-if="isFlashSupport && isLive"
        v-show="isShowDanmu"
        :list="danmuList"
        :current="progress"
        :scrollW="danmuScrollBorder"
        :status="danmuCmd"
      ></l-danmu>-->
      <span class="volume-tip" v-if="showVolumeTip">音量：{{volume}}</span>
      <!-- 控制栏 -->
      <div v-if="isFlashSupport &&!isLive" class="control">
        <div class="control-left">
          <i
            class="iconfont start-btn"
            :class="{'icon-bofang':!isPlay,'icon-zanting':isPlay}"
            @click="playHandler"
          ></i>
          <div v-if="isLive">{{liveTime}}</div>
          <div class="p-progress">
            <el-slider
              v-if="!isLive"
              v-model="progress"
              :max="videoDuration"
              :format-tooltip="formatTooltip"
              @mousedown.native="mousedownHandler"
              @mouseup.native="mouseupHandler"
            ></el-slider>
          </div>
          <div v-if="!isLive" class="video-box-time">{{playTime}}/{{allTime}}</div>
        </div>
        <div class="control-right">
          <!-- 音量 -->
          <div class="volume">
            <i
              class="iconfont"
              :class="{'icon-yinliang':!isQuiet,'icon-jingyin':isQuiet}"
              @click="isQuiet = !isQuiet"
            ></i>
            <el-slider v-model="volume" class="v-progress" vertical height="125px" :max="100"></el-slider>
          </div>
          <!-- 画质 -->
          <!-- <span class="choose-quality">
            <div class="choosed-quality">{{choosedQuality}}</div>
            <div class="quality-options">
              <div
                class="option-item"
                v-for="(item,index) in qualityOption"
                :key="index"
                :class="{'option-check':item.check}"
                @click="chooseQualityHandler(index)"
              >{{item.label}}</div>
            </div>
          </span>-->
          <!-- 弹幕屏蔽 -->
          <!-- <i
            v-if="isLive"
            class="iconfont"
            :class="{'icon-danmu':isShowDanmu,'icon-jinzhidanmu':!isShowDanmu}"
            @click="isShowDanmu = !isShowDanmu"
          ></i>-->
          <!-- 全屏 -->
          <i
            class="iconfont"
            :class="{'icon-quanping':!isFullScreen,'icon-tuichuquanping':isFullScreen}"
            @click="fullScreenHandler"
          ></i>
        </div>
      </div>
    </div>
    <div class="play-info" v-if="showOther">
      <div v-if="isLive" class="u-live-bar">
        <div class="bar">
          <a class="u-option" href="javascript:;">
            <div class="iconfont icon-shuxie"></div>
            <span>教学过程打点</span>
          </a>
          <a class="u-option" href="javascript:;">
            <div class="iconfont icon-fenxiang"></div>
            <span>分享</span>
          </a>
          <a class="u-option" href="javascript:;">
            <div class="iconfont icon-yanjing"></div>
            <span>播放(348)</span>
          </a>
          <a class="u-option" href="javascript:;">
            <div class="iconfont icon-201"></div>
            <span>订阅</span>
          </a>
        </div>
        <div class="text">
          <div class="head">学生出勤情况：</div>
          <br />
          <div class="detail">准时：25人&nbsp;&nbsp;迟到：2人&nbsp;&nbsp;旷课：1人</div>
        </div>
      </div>
      <div v-if="!isLive" class="u-select">
        <a class="u-option" href="javascript:;">
          <div class="iconfont icon-shuxie"></div>
          <span>教学过程打点</span>
        </a>
        <a class="u-option" href="javascript:;">
          <div class="iconfont icon-video"></div>
          <span>视频片段评论</span>
        </a>
        <a class="u-option" href="javascript:;">
          <div class="iconfont icon-favorite_diss"></div>
          <span>收藏(158)</span>
        </a>
        <a class="u-option" href="javascript:;">
          <div class="iconfont icon-xiazai"></div>
          <span>下载(125)</span>
        </a>
        <a class="u-option" href="javascript:;">
          <div class="iconfont icon-pinglun"></div>
          <span>评论(112)</span>
        </a>
        <a class="u-option" href="javascript:;">
          <div class="iconfont icon-yanjing"></div>
          <span>播放(348)</span>
        </a>
      </div>
    </div>
    <div class="aside" v-if="showOther">
      <ul class="asidelist">
        <el-tooltip placement="top-start">
          <div slot="content">
            <div class="er-code-box">
              <img src="../../assets/er-code.png" alt :draggable="false" />
            </div>
          </div>
          <li class="u-li">
            <i class="iconfont icon-app"></i>
          </li>
        </el-tooltip>
        <li class="u-li">
          <i class="iconfont icon-shuqian"></i>
        </li>
        <li class="u-li">
          <i class="iconfont icon-pinglun1"></i>
        </li>
        <li class="u-li">
          <i class="iconfont icon-erji"></i>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import LPlayer from "../videoPlayer/LPlayer.vue";
import http from "../../utils/http";
import api from "../../utils/api";
import h5media from "../../utils/h5media.js";
import "signalr";
let conn = {};
let hub;
export default {
  props: {
    terminal: {
      type: Boolean,
      default: false
    }
  },
  components: {
    LPlayer
  },
  data() {
    return {
      showOther: false,
      isLive: false,
      recid: "",
      onlive: 1,
      active: 1,
      count: 0,
      h5mediawebsocketURL: "",
      video1path: "",
      video2path: "",
      video3path: "",
      video1IP: "",
      video2IP: "",
      video3IP: "",
      loaded: false,
      wsSocket1: null,
      wsSocket2: null,
      wsSocket3: null,
      video1: null,
      video2: null,
      video3: null,
      //控制栏
      cameras: null,
      lastSetTime: 0,
      showVolumeTip: false,
      isFlashSupport: true,
      isMini: false,
      gotVideo: false,
      playerList: [
        { v_split: true }
        // {
        //   v_split: true,
        //   show: true,
        //   volume: 66,
        //   isSelect: true,
        //   options: {
        //     disable: true,
        //     type: "mp4",
        //     // type:"", //mp4、rtmp、flv
        //     src:
        //       "http://221.228.226.5/15/t/s/h/v/tshvhsxwkbjlipfohhamjkraxuknsc/sh.yinyuetai.com/88DC015DB03C829C2126EEBBB5A887CB.mp4"
        //   }
        // },
        // {
        //   v_split: true,
        //   show: true,
        //   volume: 66,
        //   isSelect: true,
        //   options: {
        //     disable: true,
        //     type: "rtmp",
        //     src:
        //       "rtmp://58.200.131.2:1935/livetv/hunantv"
        //   }
        // },
        // {
        //   v_split: true,
        //   show: true,
        //   volume: 66,
        //   isSelect: false,
        //   options: {
        //     disable: true,
        //     type: "mp4",
        //     src:
        //       "http://221.228.226.23/11/t/j/v/b/tjvbwspwhqdmgouolposcsfafpedmb/sh.yinyuetai.com/691201536EE4912BF7E4F1E2C67B8119.mp4"
        //   }
        // },
        // {
        //   v_split: true,
        //   show: true,
        //   volume: 66,
        //   isSelect: false,
        //   options: {
        //     disable: true,
        //     type: "mp4",
        //     src: "https://media.w3.org/2010/05/sintel/trailer.mp4"
        //   }
        // }
      ],
      rePlay: false,
      v_watch: 0,
      // cameraList: [
      //   {
      //     name: "湖南卫视",
      //     type: "mp4",
      //     src: "rtmp://58.200.131.2:1935/livetv/hunantv"
      //   },
      //   {
      //     name: "oneTV",
      //     type: "mp4",
      //     src: "rtmp://v1.one-tv.com:1935/live/mpegts.stream"
      //   },
      //   {
      //     name: "韩国朝鲜日报",
      //     type: "mp4",
      //     src: "rtmp://live.chosun.gscdn.com/live/tvchosun1.stream"
      //   }
      // ],
      // 弹幕--
      // message: "",
      // danmuCmd: "",
      // danmuScrollBorder: 0,
      // danmuList: [],
      // showDanmuTip: true,
      // isShowDanmu: true,
      // --弹幕
      progress: 0,
      currentTime: 0,
      playTime: "00:00",
      allTime: "00:00",
      volume: 66,
      videoDuration: 0,
      isPlay: true, //是否正在播放
      isQuiet: false, //是否静音
      isFullScreen: false, //是否全屏
      isDrag: false,
      loadIndex: 0,
      videoCount: 0,
      // choosedQuality: "标清",
      // qualityOption: [
      //   {
      //     check: true,
      //     label: "标清"
      //   },
      //   {
      //     check: false,
      //     label: "高清"
      //   }
      // ],
      // videoResource: [],
      liveTime: "00:00",
      // userName: "李子明"
      classroomID: "",
      rectackid: ""
    };
  },
  created() {
    if (this.$route.query.live == "true" || this.$route.query.live == true)
      this.isLive = true;
    if (this.isLive) {
      let classroomID = this.$route.query.ClassroomID;
      this.classroomID = classroomID;
      this.fetchLiveList(this.classroomID);
      this.openSocket();
    } else {
      let MaterialId = this.$route.query.MaterialId;
      if (MaterialId) {
        this.fetchVideoList(MaterialId);
      }
    }
  },
  updated() {
    if (this.isLive && !this.loaded) {
      this.openVideoSocket();
      //   h5media.onload();
      //   if (this.$refs.h5sVideo) {
      //     this.$refs.h5sVideo.load();
      //     this.$refs.h5sVideo.play();
      //     this.$refs.h5sVideo.muted = false;
      //   }
      //   if (this.$refs.h5sVideo2) {
      //     this.$refs.h5sVideo2.load();
      //     this.$refs.h5sVideo2.play();
      //   }
      //   if (this.$refs.h5sVideo3) {
      //     this.$refs.h5sVideo3.load();
      //     this.$refs.h5sVideo3.play();
      //   }
      this.loaded = true;
    }
  },
  async mounted() {
    let ConfigJson = await this.$root.GetConfigJSON();
    this.h5mediawebsocketURL = ConfigJson.Host + ":" + ConfigJson.H5Port;
    if (this.isLive) {
    }
    /*if (this.isFlashRun()) {
      this.isFlashSupport = true;
    } else {
      this.isFlashSupport = false;
    }*/
    //弹幕屏宽
    // this.danmuScrollBorder = 900;
    //监听键盘输入
    window.onkeydown = $ev => {
      if ($ev.keyCode == 32) {
        this.playHandler();
        return false;
      } else if ($ev.keyCode == 38) {
        this.volumeStepHandler(1);
      } else if ($ev.keyCode == 40) {
        this.volumeStepHandler(-1);
      }
    };
    //监听屏幕滚动
    window.onscroll = () => {
      if (!this.isLive) {
        if (window.scrollY >= 725 && this.isPlay) {
          this.isMini = true;
        } else {
          this.isMini = false;
        }
        this.zoomVideo(!this.isMini);
      }
    };
    //监听鼠标滚动
    if (document.addEventListener) {
      document.addEventListener(
        "DOMMouseScroll",
        this.mouseWheleHandler,
        false
      );
    }
    window.onmousewheel = document.onmousewheel = this.mouseWheleHandler; //IE/Opera/Chrome
    // 监听浏览器全屏状态
    this.browserFullScreenListener();
  },
  beforeDestroy() {
    this.closeVideoSocket();
  },
  methods: {
    // 开启视频流
    openVideoSocket() {
      if (this.$refs.h5sVideo) {
        //let h5spath = this.$refs.h5sVideo.getAttribute("h5spath");
        h5media.loadVideo(this.$refs.h5sVideo, this.wsSocket1, false);
        this.video1 = document.getElementById("h5sVideo");
        this.$refs.h5sVideo.volume = this.volume / 100;
        //this.video1.volume = this.volume / 100;
      }
      if (this.$refs.h5sVideo2) {
        //let h5spath = this.$refs.h5sVideo2.getAttribute("h5spath");
        h5media.loadVideo(this.$refs.h5sVideo2, this.wsSocket2, false);
        this.$refs.h5sVideo2.volume = this.volume / 100;
        this.$refs.h5sVideo2.muted = true;
      }
      if (this.$refs.h5sVideo3) {
        //let h5spath = this.$refs.h5sVideo3.getAttribute("h5spath");
        h5media.loadVideo(this.$refs.h5sVideo3, this.wsSocket3, false);
        this.$refs.h5sVideo3.muted = true;
      }
    },
    // 关闭视频流
    closeVideoSocket() {
      if (this.$refs.h5sVideo) {
        this.$refs.h5sVideo.pause();
      }
      if (this.$refs.h5sVideo2) {
        this.$refs.h5sVideo2.pause();
      }
      if (this.$refs.h5sVideo3) {
        this.$refs.h5sVideo3.pause();
      }
    },
    async openSocket() {
      let signalrURL = await api.GetSignalrURL();
      conn = $.hubConnection(signalrURL);
      hub = conn.createHubProxy("TerminalHub");
      conn.qs = { token: http.getToken() };
      var $this = this;
      hub.on("ReceiveALLRECStatus", function(jsonInfo) {
        var recList = JSON.parse(jsonInfo);
        var exist = false;
        recList.forEach(element => {
          if (
            $this.recid == element.RECID ||
            $this.classroomID == element.ClsRoomID
          ) {
            exist = true;
            if (element.LiveEstate == "0") {
              $this.onlive = 0;
            } else {
              $this.onlive = 1;
              this.loaded = false;
            }
          }
        });
        if (!exist) {
          $this.onlive = -1;
        }
      });
      conn
        .start(() => {
          /*  this.$notify({
            title: "开始",
            message: "正在开启实时状态同步",
            type: "info",
            duration: 2599
          }); */
        })
        .done(data => {
          hub.invoke("JoinGroup", "ALLREC");
          /* setTimeout(() => {
            this.$notify({
              title: "成功",
              message: "状态同步开启成功",
              type: "success",
              duration: 2599
            });
          }, 1520); */
        })
        .fail(err => {
          /* setTimeout(() => {
            conn.start();
            this.$notify.error({
              title: "错误",
              message: "状态同步开启失败"
            });
          }, 1520); */
        });
    },
    zoomVideo(sp) {
      if (this.playerList.length > 0) {
        this.$set(this.playerList[this.v_watch], "v_split", sp);
        this.playerList.forEach((element, index) => {
          if (index != this.v_watch) {
            this.$set(element, "show", sp);
          }
        });
      }
    },
    changevideo(i) {
      this.active = i;
      let $this = this;
      switch (this.active) {
        case 1:
          if (this.$refs.h5sVideo) {
            setTimeout(() => {
              $this.$refs.h5sVideo.muted = false;
            }, 100);
          }
          if (this.$refs.h5sVideo2) this.$refs.h5sVideo2.muted = true;
          if (this.$refs.h5sVideo3) this.$refs.h5sVideo3.muted = true;
          break;
        case 2:
          if (this.$refs.h5sVideo) this.$refs.h5sVideo.muted = true;
          if (this.$refs.h5sVideo2) {
            setTimeout(() => {
              $this.$refs.h5sVideo2.muted = false;
            }, 100);
          }
          if (this.$refs.h5sVideo3) this.$refs.h5sVideo3.muted = true;
          break;
        case 3:
          if (this.$refs.h5sVideo) this.$refs.h5sVideo.muted = true;
          if (this.$refs.h5sVideo2) this.$refs.h5sVideo2.muted = true;
          if (this.$refs.h5sVideo3) {
            setTimeout(() => {
              $this.$refs.h5sVideo3.muted = false;
            }, 100);
          }
          break;
      }
    },
    //获取视频资源
    async fetchVideoList(id, videoData) {
      if (videoData) {
        this.setVideoSources(videoData);
      } else {
        const res = await http.get(api.getCourseDetailInfoByID, {
          rtid: id
        });
        console.log(res);
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.setVideoSources(res.vedioData);
        }
      }
    },
    //获取该教室摄像头编码卡ID
    async fetchLiveList(rid) {
      const res = await http.get(api.getLiveVideo, {
        rid: rid
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.count = 0;
        if (res.Data.tcDeviceID) {
          this.video1path = "/" + res.Data.tcDeviceID;
          this.count++;
        }
        if (res.Data.scDeviceID) {
          this.video2path = "/" + res.Data.scDeviceID;
          this.count++;
        }
        if (res.Data.vsDeviceID) {
          this.video3path = "/" + res.Data.vsDeviceID;
          this.count++;
        }
        if (res.Data.vsDeviceID) {
          this.count += 1;
          switch (this.count) {
            case 1:
              this.video1path = "/" + res.Data.vsDeviceID;
              break;
            case 2:
              this.video2path = "/" + res.Data.vsDeviceID;
              break;
            case 3:
              this.video3path = "/" + res.Data.vsDeviceID;
              break;
          }
        }
      }
    },
    setVideoSources(arr) {
      this.videoCount = arr.length;
      // ResIISUrl + RECSID + “.mp4"
      this.playerList = [];
      if (arr.length > 0) {
        arr.forEach((element, index) => {
          let MaterialInfo = {};
          try {
            if (element.MaterialInfo)
              MaterialInfo = JSON.parse(element.MaterialInfo);
          } catch (e) {}
          this.playerList.push({
            v_split: true,
            show: true,
            volume: 66,
            isSelect: index == 0 ? true : false,
            options: {
              disable: true,
              Duration: `${MaterialInfo ? MaterialInfo.Duration : 0}`,
              type: `${MaterialInfo.Type}`,
              //"http://"+api.RECHost+":"+api.RECPort+"/"+element.Key.RECSID+".mp4";
              src: `http://${api.RECIP}:${api.RECPort}/${element.MaterialStorePath}`
              //src: `rtmp://${api.AMSIP}/vod/mp4:${element.Key.RECSID}`
            }
          });
        });
        // console.dir(this.playerList);
        this.gotVideo = true;
      }
    },
    //视频加载完成
    LLoaded($event, item) {
      if ($event.length > this.videoDuration) {
        this.playerList.forEach(element => {
          try {
            this.$set(element.options, "disable", true);
          } catch (ex) {
            console.log(ex);
          }
        });
        this.$set(item.options, "disable", false);
        this.videoDuration = $event.length;
        this.allTime = this.timeFromat($event.length);
      }
      this.loadIndex++;

      // var that = this;

      if (this.loadIndex == this.videoCount) {
      }
    },
    //视频播放状态
    LStatus($event) {
      switch ($event) {
        case "pause":
          this.danmuCmd = "pause";
          break;
        case "end":
          this.isPlay = false;
          this.rePlay = true;
          break;
        default:
          if (!this.isDrag) {
            this.progress = $event;
            this.playTime = this.timeFromat($event);
            this.liveTime = this.playTime;
          }
          break;
      }
    },
    //控制播放进度
    mousedownHandler() {
      this.isDrag = true;
      this.isPlay = false;
      this.lastSetTime = new Date().getTime();
    },
    mouseupHandler() {
      if (new Date().getTime() - this.lastSetTime > 518) {
        this.isDrag = false;
        this.isPlay = true;
      } else {
        setTimeout(() => {
          this.isDrag = false;
          this.isPlay = true;
        }, 1000);
      }
    },
    //浏览器flash支持
    isFlashRun() {
      if (typeof window.ActiveXObject != "undefined") {
        return new ActiveXObject("ShockwaveFlash.ShockwaveFlash");
      } else {
        return navigator.plugins["Shockwave Flash"];
      }
    },
    //格式化秒数
    timeFromat(time) {
      let secondTime = parseInt(time); // 秒
      let minuteTime = 0; // 分
      let hourTime = 0; // 小时
      let days = 0; //天
      if (secondTime > 60) {
        //获取分钟，除以60取整数，得到整数分钟
        minuteTime = parseInt(secondTime / 60);
        //获取秒数，秒数取佘，得到整数秒数
        secondTime = parseInt(secondTime % 60);
        //如果分钟大于60，将分钟转换成小时
        if (minuteTime > 60) {
          //获取小时，获取分钟除以60，得到整数小时
          hourTime = parseInt(minuteTime / 60);
          if (hourTime > 24) {
            days = parseInt(hourTime / 24);
            hourTime = parseInt(hourTime % 24);
          }
          //获取小时后取佘的分，获取分钟除以60取佘的分
          minuteTime = parseInt(minuteTime % 60);
        }
      }
      let result =
        "" + parseInt(secondTime) < 10
          ? "0" + parseInt(secondTime)
          : parseInt(secondTime);
      if (minuteTime > 0) {
        result =
          "" +
          (parseInt(minuteTime) < 10
            ? "0" + parseInt(minuteTime)
            : parseInt(minuteTime)) +
          ":" +
          result;
      } else {
        result = `00:${result}`;
      }
      if (hourTime > 0) {
        result =
          "" +
          (parseInt(hourTime) < 10
            ? "0" + parseInt(hourTime)
            : parseInt(hourTime)) +
          ":" +
          result;
      }
      if (days > 0) {
        result = `${parseInt(days)}天${result}`;
      }
      return result;
    },
    formatTooltip(val) {
      return `${this.timeFromat(val)}/${this.allTime}`;
    },
    //播放&暂停
    playHandler() {
      if (!this.isPlay) {
        if (this.timeFromat(this.progress) == this.allTime) {
          this.danmuCmd = "init";
        } else {
          this.danmuCmd = "restart";
        }
      }
      this.isPlay = !this.isPlay;
    },
    //全屏
    fullScreenHandler() {
      if (!this.isFullScreen) {
        this.browserFullScreen();
      } else {
        this.browserExitScreen();
      }
    },
    //浏览器全屏
    browserFullScreen() {
      let el = document.documentElement;
      let rfs =
        el.requestFullScreen ||
        el.webkitRequestFullScreen ||
        el.mozRequestFullScreen ||
        el.msRequestFullscreen;
      if (typeof rfs != "undefined" && rfs) {
        rfs.call(el);
      }
    },
    //浏览器退出全屏
    browserExitScreen() {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitCancelFullScreen) {
        document.webkitCancelFullScreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      }
      if (typeof cfs != "undefined" && cfs) {
        cfs.call(el);
      }
    },
    //监听浏览器全屏状态变化
    browserFullScreenListener() {
      if (window.addEventListener) {
        document.addEventListener("fullscreenchange", ev => {
          this.isFullScreen = !this.isFullScreen;
        });
        document.addEventListener("webkitfullscreenchange", ev => {
          this.isFullScreen = !this.isFullScreen;
        });
        document.addEventListener("mozfullscreenchange", ev => {
          this.isFullScreen = !this.isFullScreen;
        });
        document.addEventListener("MSFullscreenChange", ev => {
          this.isFullScreen = !this.isFullScreen;
        });
      }
    },
    // 全屏滚动条隐藏
    toggleScroll() {
      if (this.isFullScreen) {
        document.body.style.overflowY = "hidden";
      } else {
        document.body.style.overflowY = "scroll";
      }
    },
    //鼠标滚动事件
    mouseWheleHandler(e) {
      let $e = e || window.event;
      if ($e.wheelDelta) {
        //IE/Opera/Chrome
        this.volumeStepHandler($e.wheelDelta);
      } else if ($e.detail) {
        //Firefox
        this.volumeStepHandler($e.detail);
      }
    },
    //全屏下音量控制
    volumeStepHandler(deg) {
      if (this.isFullScreen) {
        if (deg < 0) {
          this.volume--;
          if (this.volume < 0) {
            this.volume = 0;
          }
        } else {
          this.volume++;
          if (this.volume > 100) {
            this.volume = 100;
          }
        }
        this.volumeHandler(this.volume);
        this.showVolumeTip = true;
        setTimeout(() => {
          this.showVolumeTip = false;
        }, 2599);
      }
    },
    //音量控制
    volumeHandler(degree) {
      if (this.video1) {
        this.video1.volume = degree / 100;
      }
      if (this.video2) {
        this.video2.volume = degree / 100;
      }
      if (this.video3) {
        this.video3.volume = degree / 100;
      }
    },
    //音量设置-重置
    resetVolume() {
      if (this.playerList[0].volume == 0) {
        this.volumeHandler(66);
        this.volume = 66;
      }
      this.isQuiet = false;
    },
    //音量设置-静音
    setQuiet() {
      this.volumeHandler(0);
      this.isQuiet = true;
    },
    //画质选择
    // chooseQualityHandler(index) {
    //   this.choosedQuality = this.qualityOption[index].label;
    //   this.qualityOption.forEach(element => {
    //     this.$set(element, "check", false);
    //   });
    //   document.querySelector("video").src = this.videoResource[index].src;
    //   this.$set(this.qualityOption[index], "check", true);
    // },
    watchIt(i) {
      this.v_watch = i;
      this.playerList.forEach((element, index) => {
        if (i == index) {
          this.$set(element, "isSelect", true);
        } else {
          this.$set(element, "isSelect", false);
        }
      });
    }
    // 发送弹幕
    // sendHandler() {
    //   if (this.message.replace(/^\s+|\s+$/g, "") != "") {
    //     let currentTime = this.progress;
    //     let isConflict = 25;
    //     this.danmuList.forEach(element => {
    //       if (
    //         currentTime > element.time - 0.5 &&
    //         currentTime < element.time + 0.5
    //       ) {
    //         isConflict += 28;
    //       }
    //     });
    //     this.danmuList.push({
    //       user: this.userName,
    //       msg: this.message,
    //       time: currentTime + 0.5,
    //       next: isConflict,
    //       id: this.danmuList.length,
    //       done: false
    //     });
    //     this.message = "";
    //   } else {
    //     this.showDanmuTip = true;
    //   }
    // }
  },
  watch: {
    $route(to, from) {
      if (this.$route.query.live == "true" || this.$route.query.live == true)
        this.isLive = true;
      if (this.isLive) {
        this.closeVideoSocket();
        let classroomID = this.$route.query.ClassroomID;
        this.classroomID = classroomID;
        this.fetchLiveList(this.classroomID);
        this.openSocket();
      } else {
        this.gotVideo = false;
        let MaterialId = this.$route.query.recid;
        if (MaterialId) {
          this.fetchVideoList(MaterialId);
        }
      }
    },
    volume(newVal, oldVal) {
      this.volumeHandler(newVal);
      if (newVal == 0) {
        this.isQuiet = true;
      } else {
        this.isQuiet = false;
      }
    },
    isQuiet(newVal, oldVal) {
      if (newVal) {
        this.setQuiet();
      } else {
        this.resetVolume();
      }
    },
    progress(newVal, oldVal) {
      if (this.isDrag) {
        this.currentTime = newVal;
      }
    },
    isFullScreen(newVal, oldVal) {
      this.zoomVideo(!newVal);
      this.toggleScroll();
      this.changevideo(this.active);
    }
    // message(newVal, oldVal) {
    //   if (newVal.replace(/^\s+|\s+$/g, "") != "") {
    //     this.showDanmuTip = false;
    //   } else {
    //     this.showDanmuTip = true;
    //   }
    // }
  }
};
</script>
<style lang="less">
.video-box {
  width: 1200px;
  height: 500px;
  background: #000;
  display: flex;
  justify-content: space-between;
  user-select: none;
  z-index: 6;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  .div_1 {
    width: 100%;
    height: 100%;
    position: relative;
    .live-video-box {
      position: absolute;
      background: black;
      div {
        width: 100%;
        height: calc(100% - 30px);
        position: absolute;
        left: 0;
      }
      video {
        width: 100%;
        height: 100%;
        left: 0;
        position: absolute;
      }
    }
  }
  .second {
    .video1 {
      width: 100%;
      height: 100%;
      left: 0;
      z-index: 1;
    }
    .video2 {
      width: 150px;
      height: 75px;
      right: 80px;
      bottom: 80px;
      z-index: 2;
    }
  }
  .third {
    .video1 {
      width: 60%;
      height: 100%;
      left: 0;
    }
    .video2 {
      width: 40%;
      height: 50%;
      right: 0;
      top: 0;
    }
    .video3 {
      width: 40%;
      height: 50%;
      right: 0;
      bottom: 0;
    }
  }
  //播放器
  .play-box {
    width: 100%;
    height: 100%;
    position: relative;
    background: #000;
    .l-player-0 {
      width: 66%;
      height: 89%;
      left: 6px;
      top: 8px;
    }
    .l-player-1 {
      width: 32%;
      height: 44%;
      top: 8px;
      right: 6px;
    }
    .l-player-2 {
      width: 32%;
      height: 43%;
      bottom: 48px;
      right: 6px;
    }
    // flash提示
    .flash-tip {
      width: 225px;
      position: absolute;
      top: 36%;
      left: 50%;
      transform: translate(-50%);
      color: #bbb;
      z-index: 99;
      a {
        color: orange;
      }
    }
    .player-container {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      z-index: 1;
    }
    .volume-tip {
      display: inline;
      padding: 1px 5px 2px;
      border-radius: 5px;
      position: absolute;
      top: 58px;
      right: 125px;
      color: #fff;
      background: rgba(0, 0, 0, 0.5);
      z-index: 8;
      transition: all 0.6s;
    }
    .control {
      position: absolute;
      bottom: 0;
      height: 40px;
      width: 100%;
      background: rgba(66, 66, 66, 0.6);
      color: #fff;
      z-index: 3;
      user-select: none;
      .iconfont {
        font-size: 1.36rem !important;
        &:hover {
          cursor: pointer;
        }
      }
      .control-left,
      .control-right {
        height: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
      }
      .control-left {
        float: left;
        width: 92%;
        .start-btn {
          padding-left: 18px;
          font-size: 1.8rem !important;
        }
        .p-progress {
          width: 82%;
        }
        .video-box-time {
          line-height: 40px;
        }
      }
      .control-right {
        width: 8%;
        float: right;
        .blue-text {
          color: #00abfa;
        }
        .volume {
          width: 40px;
          height: 100%;
          line-height: 40px;
          text-align: center;
          position: relative;
          .iconfont {
            left: 50%;
            transform: translateX(-50%);
            font-size: 1.1rem !important;
          }
          i,
          .v-progress {
            position: absolute;
          }
          .v-progress {
            transform: translateY(-100%);
            left: 0;
            background: rgba(66, 66, 66, 0.6);
            box-shadow: 0 0 8px #aaa inset;
            border-radius: 6px;
            padding: 12px 0;
            display: none;
          }
          &:hover {
            cursor: pointer;
            .v-progress {
              display: inline;
            }
          }
        }
        // 画质选择
        // .choose-quality {
        //   width: 32px;
        //   height: 100%;
        //   line-height: 37px;
        //   position: relative;
        //   font-size: 0.9rem;
        //   .choosed-quality,
        //   .quality-options {
        //     position: absolute;
        //     left: 0;
        //   }
        //   .quality-options {
        //     width: 42px;
        //     background: #202225;
        //     box-shadow: 0 0 8px #aaa inset;
        //     border-radius: 8px;
        //     padding: 5px 0;
        //     left: -8px;
        //     transform: translateY(-100%);
        //     font-size: 0.8rem;
        //     text-align: center;
        //     display: none;
        //     .option-item {
        //       width: 100%;
        //       margin-bottom: 3px;
        //     }
        //     .option-check {
        //       color: #317fff;
        //     }
        //   }
        //   &:hover {
        //     cursor: pointer;
        //     .quality-options {
        //       display: block;
        //     }
        //   }
        // }
        .icon-danmu {
          color: orange;
          text-shadow: 2px 0 5px violet;
        }
      }
      &:hover {
        opacity: 1;
      }
    }
  }
  // mini模式
  .mini-player {
    width: 300px !important;
    height: 169px !important;
    position: fixed;
    bottom: 200px;
    right: 158px;
    z-index: 99999;
    box-shadow: 0 0 8px #00abfa;
    .l-player-0 {
      left: 0 !important;
      top: 0 !important;
    }
    .control {
      display: none;
    }
    .play-btn span {
      display: none;
    }
  }
}
//全屏样式
.full-screen-mode {
  position: static !important;
  transform: none !important;
  .play-box {
    width: 100vw !important;
    height: 100vh !important;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 999;
    .control {
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100vw;
      opacity: 0;
      .p-progress {
        width: 88% !important;
      }
    }
  }
}
// slider样式
.v-progress:focus,
.p-progress:focus {
  outline: none;
}
//底部选项
.play-info {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 80px;
  background: #161718;
  width: 100%;
  .u-select {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .u-option {
    display: inline-block;
    height: 100%;
    width: 100px;
    padding-top: 15px;
    text-align: center;
    line-height: 30px;
    text-decoration: none;
    color: #757676;
    .iconfont {
      font-size: 1.8rem !important;
    }
    &:hover {
      color: #fff;
    }
  }
  .u-live-bar {
    width: 100%;
    height: 100%;
    .bar {
      width: 52%;
      height: 100%;
      float: left;
      display: flex;
      justify-content: space-around;
    }
    .text {
      float: right;
      margin: 8px;
      text-align: center;
      .head {
        color: #fff;
      }
      .detail {
        color: silver;
      }
    }
  }
}

/*===侧边栏部分===*/

.aside {
  box-sizing: border-box;
  position: absolute;
  top: 180px;
  left: 100%;
  margin-left: 20px;
}

.asidelist {
  float: left;
  height: 240px;
  width: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  background: #fff;
  border: 3px solid #000;
  border-left: 2px solid #000;
  border-right: 1px solid #000;
  .u-li {
    box-sizing: border-box;
    height: 58px;
    border: 1px solid #eee;
    text-align: center;
    line-height: 58px;
    color: #aaa;
    .iconfont {
      font-size: 1.8rem !important;
    }
    &:hover {
      cursor: pointer;
      background: #317fff;
      color: #fff;
    }
  }
}
.er-code-box {
  width: 125px;
  height: 125px;
  img {
    width: 100%;
    height: 100%;
  }
}
.video-box-time {
  -moz-user-select: none;
  -o-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.video-controller {
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 100%;
  background: rgba(66, 66, 66, 0.6);
  color: #fff;
  z-index: 3;
  user-select: none;
  opacity: 0;
  .iconfont {
    font-size: 1.36rem !important;
    &:hover {
      cursor: pointer;
    }
  }
  .control-left,
  .control-right {
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .control-left {
    float: left;
    .start-btn {
      padding-left: 18px;
      font-size: 1.8rem !important;
    }
  }
  .control-right {
    width: 12%;
    float: right;
    .blue-text {
      color: #00abfa;
    }
    .volume {
      width: 40px;
      height: 100%;
      line-height: 40px;
      text-align: center;
      position: relative;
      .iconfont {
        left: 50%;
        transform: translateX(-50%);
        font-size: 1.1rem !important;
      }
      i,
      .v-progress {
        position: absolute;
      }
      .v-progress {
        transform: translateY(-100%);
        left: 0;
        background: rgba(66, 66, 66, 0.6);
        box-shadow: 0 0 8px #aaa inset;
        border-radius: 6px;
        padding: 12px 0;
        display: none;
      }
      &:hover {
        cursor: pointer;
        .v-progress {
          display: inline;
        }
      }
    }
    .icon-danmu {
      color: orange;
      text-shadow: 2px 0 5px violet;
    }
  }
  &:hover {
    opacity: 1;
  }
}
// 嵌入式web使用
.terminal-mode {
  width: 100%;
  height: 100%;
  .play-box .control {
    .control-left {
      width: 88%;
      .p-progress {
        width: 69%;
      }
    }
    .control-right {
      width: 12%;
    }
  }
}
</style>



