// import api from "../utils/api";

function H5SWebSocketClient(h5mediawebsocketURL, h5spath) {
  var socket;
  try {
    //alert(window.location.protocol);
    if (window.location.protocol == "http:") {
      socket = new WebSocket('ws://' + h5mediawebsocketURL + h5spath);
      // console.dir(socket);
    }
    if (window.location.protocol == "https:") {
      //alert(window.location.host);
      // console.log(window.location.host);
      socket = new WebSocket('wss://' + h5mediawebsocketURL + h5spath);
    }
  } catch (e) {
    Console.log(e);
    return;
  }
  return socket;
}

function H5Media(video, wsSocket, muted) {
  var sourceBuffer;
  var buffer = [];
  var mediaSource;
  //var wsSocket;

  var readFromBuffer = function () {
    if (buffer.length === 0 || !sourceBuffer || sourceBuffer.updating) {
      return;
    }
    try {
      var data = buffer.shift();
      sourceBuffer.appendBuffer(data);
    } catch (e) {
      /*  console.log(e); */
    }
  };

  function keepaliveTimer(obj) {
    if (obj)
      if (obj.readyState === 1)
        obj.send("keepalive");
  }

  function onWebSocketData(msg) {
    var blob = msg.data;

    var fileReader = new FileReader();
    fileReader.onload = function () {
      buffer.push(this.result);
      readFromBuffer();
    };

    fileReader.readAsArrayBuffer(blob);
  }

  window.MediaSource = window.MediaSource || window.WebKitMediaSource;
  if (!window.MediaSource) {
    console.log('MediaSource API is not available');
  }

  mediaSource = new window.MediaSource();

  if (video) {
    var h5spath = video.getAttribute('h5spath');
    var h5mediawebsocketURL = video.getAttribute("h5mediawebsocketURL");
    video.src = window.URL.createObjectURL(mediaSource);
    mediaSource.addEventListener('sourceopen', function () {
      var strCodec = 'video/mp4; codecs="avc1.640029, mp4a.40.2"';
      sourceBuffer = mediaSource.addSourceBuffer(strCodec);
      mediaSource.duration = Infinity;
      sourceBuffer.addEventListener('updateend', readFromBuffer);
    });

    video.addEventListener('play', function () {
      wsSocket = H5SWebSocketClient(h5mediawebsocketURL, h5spath);
      wsSocket.onmessage = onWebSocketData;
      setInterval(function () {
        keepaliveTimer(wsSocket);
      }, 1000);
    }, false);

    video.addEventListener('pause', function () {
      // console.log("wsSocket.close");
      wsSocket.close();
      sourceBuffer = null;
      buffer = null;
      mediaSource = null;
      wsSocket = null;
      video = null;
    });

    video.load();
    video.play();
    video.muted = muted;
  }
}

export default {
  loadVideo(video, wsSocket, muted) {
    H5Media(video, wsSocket, muted);
  },
  webSocketClient(h5mediawebsocketURL, path) {
    return H5SWebSocketClient(h5mediawebsocketURL, path);
  }
}



