<template>
  <div class="detail-content">
    <div class="item-box" v-for="(item, index) in datas" :key="index">
      <div class="item-box_left">
        <div class="box-title">{{item.title}}</div>
        <div class="box-status">
          <span>时长：{{item.duration}}</span>
          <span>大小：{{item.size}}</span>
        </div>
      </div>
      <div class="item-box_right">
        <div class="box-top">
          <span>评论人员：</span>{{item.commentator}}
          <span class="box-start">评分：</span>
          <el-rate
            class="start-rate"
            v-model="item.star"
            disabled
            allow-half
            :colors="['red', 'green', '#ff9900']"
            show-score
          ></el-rate>
        </div>
        <div class="box-bottom">
          <span>议论点评：</span>{{item.comment}}
        </div>
      </div>
      <span class="box-date">{{item.dateTime}}</span>
    </div>
    <div v-if="datas.length > 0" class="course-page" style="margin-top:28px">
      <el-pagination
        layout="prev, pager, next, jumper"
        :page-size="pageSize"
        :current-page.sync="currentPage"
        :total="total"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    datas: {
      type: Array
    }
  },
  data() {
    return {
      pageSize: 6,
      currentPage: 1,
      total: 1
    }
  },
  methods: {
    handleCurrentChange(val) {}
  }
};
</script>
<style lang="less" scoped>
.item-box {
  height: 110px;
  width: 100%;
  padding: 25px 18px;
  border-bottom: 1px solid #ccc;
  color: #333;
}

.item-box_left,
.item-box_right {
  float: left;
  display: flex;
  height: 100%;
  padding-right: 20px;
  flex-direction: column;
  justify-content: space-between;
}

.box-title {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.item-box_left {
  width: 230px;
}

.box-status {
  display: flex;
  justify-content: space-between;
  color: #999;
}

.item-box_right {
  padding-left: 30px;
  border-left: 1px solid #ccc;
}

.box-start {
  margin-left: 58px;
  color: #999;
}

.box-date {
  float: right;
  color: #999;
}
.start-rate {
  float: right;
  margin-right: 25px;
}
</style>



