<template>
  <div class="wrap" style="text-align:left;overflow:hidden">
    <div class="wrapper" :class="{'player-wrapper': isScroll}">
      <div class="top">
        <i class="iconfont icon-weizhi"></i>
        <span>当前位置：</span>
        <span>
          <a class="nav-path" href="/#/main/index">首页</a>
        </span>&nbsp;
        <span>
          >
          <router-link
            class="nav-path"
            :to="lastPage=='精品课程'?'/main/course/page':'/main/live' "
          >{{lastPage}}</router-link>
          <!-- <a class="nav-path" :href="lastPage=='精品课程'?'/main/course/page':'/main/live' ">12{{lastPage}}</a> -->
        </span>&nbsp;
        <span class="active-link">> {{courseName}}</span>
      </div>
      <video-box :isLive="isLive" :recid="recid" ref="videoBox"></video-box>
    </div>
    <!-- <div v-if="isScroll" class="h596"></div> -->
    <div class="course-content">
      <div class="course-infor-box">
        <div class="course-infor-wrap border bg-white">
          <div class="head">课程信息</div>
          <div class="course-infor" v-loading="inforLoading">
            <div>
              课程名称：
              <strong>{{courseName}}</strong>
            </div>
            <div v-show="clsesson!=''">
              课节：
              <strong>{{clsesson}}</strong>
            </div>
            <div>
              <span>
                老师：
                <strong>{{teachers}}</strong>
              </span>
              <span>
                教室：
                <strong>{{clsroom}}</strong>
              </span>
            </div>
            <div>
              <!-- <span>
                专业：
                <strong>{{specName}}</strong>
              </span>-->
              <span>
                班级：
                <strong>{{classname}}</strong>
              </span>
            </div>
            <div>
              日期：
              <strong>{{startDate}}</strong>
            </div>
          </div>
        </div>
        <el-tabs class="course-detail border bg-white" v-model="tabName" v-if="false">
          <el-tab-pane label="评论" name="first">
            <!-- 评论 -->
            <comment
              ref="comment"
              :page-size="pageSize"
              :total="allItems"
              :commentList="commentList"
              :recid="hasrecid"
              @submitcomment="Submitcomment"
              @thumbup="ThumbUp"
              @pageChange="GetComment"
            ></comment>
          </el-tab-pane>
          <el-tab-pane label="量表打分" name="second">
            <!-- 量表打分 -->
            <scale
              :textarea="text"
              :trees="scals"
              :recid="hasrecid"
              @markscore="SaveGauge"
              :submitdisabled="scalesubmitdisabled"
            ></scale>
          </el-tab-pane>
          <el-tab-pane label="打点评分" name="third">
            <!-- 打点评分 -->
            <points :datas="points"></points>
          </el-tab-pane>
          <el-tab-pane v-if="!isLive" label="课堂活动" name="fourth">
            <!-- 课堂活动 -->
            <actives></actives>
          </el-tab-pane>
        </el-tabs>
      </div>
      <side-recommend class="border" :title="recommendTitle" :live="isLive"></side-recommend>
    </div>
    <footer-infor></footer-infor>
  </div>
</template>
<script>
import videoBox from "../../components/course/videoBox";
import sideRecommend from "../../components/sideRecommend";
import comment from "../../components/course/detailComment";
import actives from "../../components/course/detailActivities";
import points from "../../components/course/detailPoints";
import scale from "../../components/course/detailScale";
import http from "../../utils/http";
import api from "../../utils/api";
import { formatDate } from "../../utils/utils.js";
import footerInfor from "../../components/footerInfor";
export default {
  components: {
    videoBox,
    sideRecommend,
    comment,
    actives,
    points,
    scale,
    footerInfor
  },
  data() {
    return {
      isScroll: false,
      text: "", //评语
      pageSize: http.pageSize,
      allItems: 50,
      isLive: false,
      recid: "",
      csid: "",
      hasrecid: false,
      lastPage: "",
      MaterialId: "",
      tabName: "first",
      resIISUrl: "",
      courseName: "",
      teachers: "",
      clsroom: "",
      startDate: "",
      clsesson: "",
      specName: "",
      classname: "",
      recommendTitle: "",
      scals: [],
      scalesubmitdisabled: false,
      evalpid: "",
      inforLoading: true,
      userid: "",
      username: "",
      commentList: [
        // {
        //   imgSrc: require("../../assets/yuner.jpg"),
        //   name: "林允儿",
        //   score: 5.0,
        //   feel: 1,
        //   context: "just so so",
        //   createTime: "4小时前",
        //   thumbuped: true,
        //   thumbups: 1
        // }
      ],
      points: [
        {
          title: "系统工作原理",
          duration: "01:17",
          size: "3.1MB",
          commentator: "张志忠",
          star: 4,
          comment: "讲的非常好啊，知识点和难点都剖析得很清晰~!",
          dateTime: "2018-06-21 10:45:34"
        }
      ]
    };
  },
  mounted() {
    window.scrollTo(0, 0);
    window.onscroll = () => {
      if (window.scrollY >= 120) {
        this.isScroll = true;
      } else {
        this.isScroll = false;
      }
    };
    this.isLive = this.$route.query.live;
    this.recid = this.$route.query.recid;
    if (this.recid) {
      this.hasrecid = true;
    } else {
      this.courseName = this.$route.query.roomname;
      this.startDate = this.$route.query.date + " " + this.$route.query.time;
    }
    if (this.isLive) {
      this.lastPage = "在线直播";
      this.recommendTitle = "相关直播";
    } else {
      this.lastPage = "精品课程";
      this.recommendTitle = "相关课程";
    }
    if (!this.isLive) {
      this.MaterialId = this.$route.query.recid;
      this.GetCourseDetail(this.MaterialId);
    } else {
      this.GetLiveCourseDetail(this.recid);
    }
    if (http.logined()) {
      // 已登录
      let userInfo = http.getUser();
      if (userInfo) {
        this.userid = http.getUser().UserID;
        this.username = http.getUser().UserName;
      }
      this.GetGauge();
      this.GetComment();
    }
  },
  methods: {
    async GetCourseDetail(id) {
      const res = await http.get(api.getCourseDetailInfoByID, {
        rtid: id
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.inforLoading = false;
        if (res.Datas && res.Task) {
          let ScheduleInfo = {};

          try {
            if (res.Task.ScheduleInfo)
              ScheduleInfo = JSON.parse(res.Task.ScheduleInfo);
          } catch (e) {}

          let RTInfo = {};
          try {
            if (res.Task.RTInfo) RTInfo = JSON.parse(res.Task.RTInfo);
          } catch (e) {}

          this.courseName =
            ScheduleInfo.CourseName == ""
              ? res.Task.RTName
              : ScheduleInfo.CourseName;
          this.clsroom = RTInfo.ClsRoom.ClsRoomName;
          try {
            let ThcJson = JSON.parse(ScheduleInfo.Teachers);
            for (let i in ThcJson) {
              this.teachers += `${ThcJson[i]}、`;
            }
            this.teachers = this.teachers.substring(
              0,
              this.teachers.length - 1
            );
          } catch (e) {}
          this.startDate = formatDate(res.Task.RTStart, "yyyy-MM-dd");
          this.clsesson =
            formatDate(res.Task.RTStart, "hh:mm") +
            "-" +
            formatDate(res.Task.RTEnd, "hh:mm");
          if (ScheduleInfo.Classes) {
            let ClassJson = JSON.parse(ScheduleInfo.Classes);
            for (let i in ClassJson) {
              this.classname += `${ClassJson[i]}、`;
            }
            this.classname = this.classname.substring(
              0,
              this.classname.length - 1
            );
          }
          this.recid = res.Task.RTID;
          this.csid = ScheduleInfo.BaseInfo ? ScheduleInfo.BaseInfo.CSID : "";
        }
        this.vedioData = res.Datas;
        this.$refs.videoBox.fetchVideoList("", res.Datas);
      }
    },
    async GetLiveCourseDetail(id) {
      const res = await http.get(api.getLiveMsg, {
        Id: id
      });
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: res.message,
          type: res.type
        });
      } else {
        this.inforLoading = false;
        let data = res.Data;
        if (data) {
          this.courseName = res.Data.Key.RECTitle;
          this.clsroom = res.Data.RoomName;
          this.teachers = res.Data.TeacherName;
          this.startDate = formatDate(res.Data.Key.RECStartTime, "yyyy-MM-dd");
          this.clsesson = `${formatDate(
            res.Data.Key.RECStartTime,
            "hh:mm"
          )}-${formatDate(res.Data.Key.RECEndTime, "hh:mm")}`;
          this.specName = res.Data.Key.Specialized;
          this.classname = res.Data.ClsName;
        }
      }
    },
    async GetGauge() {
      if (this.csid) {
        const res = await http.get(api.getGauge, {
          uid: this.userid,
          csid: this.csid
          // materialid: this.MaterialId
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.scals = [];
          if (res.auth > 0) {
            this.scalesubmitdisabled = false;
          } else {
            this.scalesubmitdisabled = true;
          }
          if (res.evalpid) {
            this.evalpid = res.evalpid;
          }
          if (res.desc) {
            this.text = res.desc;
          }
          res.Datas.forEach(element => {
            let child = [];
            if (element.child) {
              element.child.forEach(subelement => {
                let score = 4;
                if (subelement.Value) {
                  score = parseFloat(subelement.Value);
                } else {
                }
                child.push({
                  GIID: subelement.Key.GIID,
                  GIName: subelement.Key.GIName,
                  GIContent: subelement.Key.GIContent,
                  GIScore: subelement.Key.GIScore,
                  GSScore: score,
                  disabled: this.scalesubmitdisabled
                });
              });
            }
            this.scals.push({
              GaugeName: element.Key.GIName,
              GIID: element.Key.GIID,
              isFold: true,
              showHeadOpts: false,
              edit: false,
              children: child
            });
          });
        }
      }
    },
    async SaveGauge(text) {
      if (this.userid) {
        const res = await http.post(api.saveGauge, {
          uid: this.userid,
          recid: this.recid,
          evalpid: this.evalpid,
          text: text,
          msg: JSON.stringify(this.scals).toString()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.scalesubmitdisabled = true;
          this.text = text;
          this.$message({
            showClose: true,
            message: "评价成功！",
            type: "success"
          });
        }
      } else {
        this.$message({
          showClose: true,
          message: "请先登录",
          type: "warning"
        });
      }
    },
    async Submitcomment() {
      if (this.userid) {
        const res = await http.post(api.saveComment, {
          uid: this.userid,
          recid: this.recid,
          username: this.username,
          content: this.$refs.comment.textarea,
          rate: this.$refs.comment.scoreValue,
          facility: this.$refs.comment.degress,
          replyid: this.$refs.comment.replyid
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.GetComment();
        }
      } else {
        this.$message({
          showClose: true,
          message: "请先登录",
          type: "warning"
        });
      }
    },
    async GetComment(val) {
      if (this.userid && this.recid) {
        const res = await http.get(api.getComment, {
          uid: this.userid,
          recid: this.recid,
          pno: val,
          psize: this.pageSize
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.commentList = [];
          this.allItems = res.RowsCount;
          this.pageSize = res.PageSize;
          res.Datas.forEach(element => {
            this.commentList.push({
              imgSrc: require("../../assets/boy_img.png"),
              lecid: element.Key.LECID,
              name: element.Key.UserName,
              score: element.Key.LECRate,
              feel: element.Key.LECFacility,
              context: element.Key.LECContent,
              createTime: formatDate(
                element.Key.LECCreateDate,
                "yyyy-MM-dd hh:mm:ss"
              ),
              thumbups: element.Value
              // thumbuped: false 用户是否对该条评论点过赞
            });
          });
        }
      } else {
        /* this.$message({
          showClose: true,
          message: "请先登录",
          type: "warning"
        }); */
      }
    },
    async ThumbUp(item) {
      if (this.userid) {
        const res = await http.post(api.ThumbUp, {
          lid: item.lecid,
          uid: this.userid
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          this.GetComment();
        }
      } else {
        this.$message({
          showClose: true,
          message: "请先登录",
          type: "warning"
        });
      }
    }
  }
};
</script>
<style lang="less" scoped>
.wrapper {
  box-sizing: border-box;
  width: 100%;
  min-width: 1200px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 36px;
  background: #3f4857;
}
// .player-wrapper {
// position: fixed;
// top: 0;
// left: 0;
// z-index: 999;
// }
.nav-path {
  text-decoration: none;
  font-size: 16px;
  color: #fff;
}
.detail-item {
  display: flex;
  justify-content: space-between;
}

.active-link {
  color: #fff;
}

.active {
  border-bottom: 2px solid #63a9ff;
}

.u-option .iconfont {
  font-size: 30px !important;
}

.top .iconfont {
  margin-right: 6px;
  font-size: 18px !important;
}

.top {
  display: flex;
  height: 20px;
  width: 1200px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 20px;
  color: #bfcbe1;
}
.course-infor-box {
  width: 955px;
  float: left;
}
.course-infor-wrap {
  border-bottom: 1px solid #eee;
  margin-bottom: 18px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  .head {
    width: 100%;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #eee;
    padding-left: 12px;
    background: #63a9ff;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    color: #fff;
  }
  .course-infor {
    padding: 12px;
    padding-top: 0;
    div {
      margin-top: 12px;
      span {
        margin-right: 12px;
      }
    }
  }
}
</style>



